<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <nav class="navbar navbar-expand-lg bg-light">
    <div class="container-fluid">
      <a class="navbar-brand" href="#">LOGO</a>
      <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav"
        aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNav">
        <ul class="navbar-nav">
          <li class="nav-item">
            <router-link to="/" class="nav-link active">首頁</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/about" class="nav-link text-dark">關於</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/login" class="nav-link text-dark">登入</router-link>
          </li>
          <li class="nav-item dropdown">
            <a class="dropdown-toggle btn border-0 text-dark p-2" href="#" role="button" data-bs-toggle="dropdown"
              aria-expanded="false">
              後台
            </a>
            <ul class="dropdown-menu">
              <li>
                <a class="dropdown-item" href="#">
                  <router-link to="/DashBoard/productslist" class="nav-link text-dark p-0">產品列表
                  </router-link>
                </a>
              </li>
              <li>
                <a class="dropdown-item" href="#">
                  <router-link to="/DashBoard/order" class="nav-link text-dark p-0">訂單管理</router-link>
                </a>
              </li>
              <li>
                <a class="dropdown-item" href="#">
                  <router-link to="/DashBoard/Coupon" class="nav-link text-dark p-0">優惠券</router-link>
                </a>
              </li>
            </ul>
          </li>
          <li class="nav-item">
            <a href="#" class="nav-link text-dark" @click="logOut">登出</a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import Collapse from 'bootstrap/js/dist/collapse'
import DropDown from 'bootstrap/js/dist/dropdown'
export default {
  methods: {
    logOut () {
      const api = `${process.env.VUE_APP_PATH}logout`
      this.axios.post(api).then((res) => {
        if (res.data.success === true) {
          this.$router.push('./login')
        }
      })
    }
  },
  created () {
    // collase
    const collapseElementList = document.querySelectorAll('.collapse')
    const collapseList = [...collapseElementList].map(collapseEl => new Collapse(collapseEl))
    collapseList.forEach((item) => {
      item.hide()
    })

    // dropDown
    const dropdownElementList = document.querySelectorAll('.dropdown-toggle')
    const dropdownList = [...dropdownElementList].map(dropdownToggleEl => new DropDown(dropdownToggleEl))
    dropdownList.forEach((item) => {
    })
  }
}
</script>
